/* 
  Allow angular.js to be loaded in body, hiding cloaked elements until 
  templates compile.  The !important is important given that there may be 
  other selectors that are more specific or come later and might alter display.  
 */
[ng\:cloak], [ng-cloak], .ng-cloak {
  display: none !important;
}

.general-info {
    color:#799411;
}

.warning {
    color:#f00;
}

.slide-down {
    overflow:hidden;
}

.slide-down.ng-enter, .slide-down.ng-leave {
    max-height:0;
    -webkit-transition: all 500ms;
}

.slide-down.ng-enter-active  {
    max-height:500px;
}

.page-wrapper {
    width:960px;
    margin:30px auto;  
}

.page-wrapper img {
    max-width:100%;
}

.page-mask {
    background:#fff;
    position:fixed;
    left:0;
    top:0;
    width:100%;
    height:100%;
    z-index:100;
    display:table;
}

.page-mask.ng-leave {
    -webkit-transition: all 500ms;
    opacity:1;
}

.page-mask.ng-leave-active {
    opacity:0;
}

.page-mask span {
    display:table-cell;
    vertical-align:middle;
    text-align:center;
}

select, input {
    padding:5px;
    border-color:#ccc;
    font-size:12px;
}


.call-to-action {
    color:#333;
    padding:5px 15px;
    font-weight:bold;
    background:#fc0;
    outline:none;
}

.call-to-action:hover {
    text-decoration:none;
    color:#666;
}

.general-action {
    padding:0 5px;
    background:#ffeda6;
    border:none;
    font-size:11px;
    color:#666;
    outline:none;
    display:inline-block;
}

.general-action:hover {
    color:#000;
}

.general-action-2 {
    padding: 4px 15px;
    border: #fecd07 solid 3px;
    background: #fff;
    font-size: 12px;
    outline:none;
}

.general-action-2:hover {
    color:#666;
    text-decoration:none;
}

hr {
    width:100px;
}

.col-wrapper {
    margin-top:20px;
}

.col-main {
    width:75%;
    float:left;
    margin-top:20px;
}

.col-summary {
    width:22%;
    float:right;
    padding: 0 0 20px 20px;
    margin-top: 20px;
    font-size:12px;
    border-left:1px solid #ccc;
}

.col-summary select {
    width:100%;
}

.col-summary h1 {
    background:#000;
    color:#fff;
    font-size:16px;
    text-align:center;
    padding:2px 0;
}

.col-summary h2 {
    font-size:14px;
}

.col-summary hr {
    width:100%;
}

.float-left {
float:left;
}

.float-right {
float:right;
}

a {
    text-decoration:none;
}

a:hover {
    text-decoration:underline;
}

.booking-engine-header {
    height:0;
    position:relative;
    overflow:hidden;
    margin:8px -20px 0 -20px;
    padding-bottom:25%;
}

.booking-engine-header img {
    height:100%;
    width:100%;
}

.booking-engine-title {
    position:absolute;
    left:0;
    bottom:0;
    margin:0;
    padding:15px 20px;
    color:#fff;
    font-size:25px;
    z-index:1;
}


.booking-engine-header:after {
    content:"";
    position:absolute;
    left:0;
    top:0;
    height:100%;
    width:75%;
    background: -moz-linear-gradient(left,  rgba(0,0,0,0.65) 0%, rgba(0,0,0,0) 100%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, right top, color-stop(0%,rgba(0,0,0,0.65)), color-stop(100%,rgba(0,0,0,0))); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(left,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(left,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%); /* Opera 11.10+ */
    background: -ms-linear-gradient(left,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%); /* IE10+ */
    background: linear-gradient(to right,  rgba(0,0,0,0.65) 0%,rgba(0,0,0,0) 100%); /* W3C */
}


.booking-engine-page.ng-enter {
    -webkit-transition: all 500ms;
    opacity:0;
    -webkit-transform: translateY(10px);
}

.booking-engine-page.ng-enter-active {
    opacity:1;
    -webkit-transform: translateY(0);
}

