.date-selector-wrapper {
    font-size:12px;
    border-top:4px solid #ccc;
    margin-bottom:30px;
}

.date-selector-wrapper h1 {
    font-size:15px;
    margin:0;
    text-transform:uppercase;
}

.date-selector-wrapper h2 {
    font-size:11px;
    margin:0;
    font-weight:normal;
    color:#666;
    text-transform:uppercase;
}

.date-selector-wrapper h3 {
    font-size:11px;
    margin:0;
    font-weight:normal;
    color:#799411;
}

.date-selector-date {
    background:#eee;
    margin-top:20px;
    height:40px;
    line-height:20px;
    padding:10px;
    cursor:pointer;
    -webkit-transition: all 500ms;
}

.date-selector-arrow {
    background:#ccc;
    border-radius:50%;
    height:20px;
    width:20px;
    line-height:20px;
    margin-top:-10px;
    text-align:center;
    display:inline-block;
    color:#fff;
}

.date-selector-date label {
    cursor:pointer;
}

.date-selector-date.active {
    background:#ddd;
}

.date-selector-date input {
    height:20px;
}