body {
    color:#333;
}

* {
    box-sizing:border-box;
}

.float-left {
float:left;
}

.float-right {
float:right;
}

.trip-wrapper select, .trip-wrapper input {
    padding:5px;
    border-color:#ccc;
}

.trip-wrapper {
    margin: 20px auto;
    font-size:12px;
    position:relative;
}

.trip-wrapper a {
    background:#ffeda6;
    color:#666;
    padding:1px 4px;
    font-weight:bold;   
    text-decoration:none;
}

.trip-wrapper a:hover {
    color:#000;
}

.trip-continent-country-wrapper {
    overflow:hidden;
}

.trip-continent-wrapper {
    height:100%;
    width:50%;
    border-right:1px solid #ddd;
    float:left;
    padding-right:20px;
}

.trip-continent {
    width:100%;
    height:100%;
}

.trip-continent.ng-enter {
    opacity:0;
    -webkit-transition: opacity 500ms;
}

.trip-continent.ng-enter-active {
    opacity:1;
}

.trip-continent img {
    margin-bottom:10px;
    width:100%;
    height:210px;        
}

.trip-country-wrapper {
    height:100%;
    width:50%;
    float:left;
    padding-left:20px;
}

.trip-country {
    width:100%;
    height:100%;

}

.trip-country.ng-enter {
    opacity:0;
    -webkit-transition: opacity 500ms 500ms;
}

.trip-country.ng-enter-active {
    opacity:1;
}

.trip-country-image-wrapper {
    margin-bottom:10px;
    position:relative;
    overflow:hidden;
    height:180px;
}

.trip-country-image-wrapper h2 {
    position:absolute;
    top:50%;
    line-height:40px;
    height:40px;
    width:100%;
    color:#fff;
    margin-top:-20px;
    text-align:center;
    text-transform:uppercase;
    text-shadow: 0 0 5px #000;
    font-size:24px;
}

.trip-country-image-wrapper:before {
    content:"";
    position:absolute;
    bottom:0;
    left:0;
    width:100%;
    height:33%;
    background: -moz-linear-gradient(top,  rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%); /* FF3.6+ */
    background: -webkit-gradient(linear, left top, left bottom, color-stop(0%,rgba(0,0,0,0)), color-stop(100%,rgba(0,0,0,1))); /* Chrome,Safari4+ */
    background: -webkit-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* Chrome10+,Safari5.1+ */
    background: -o-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* Opera 11.10+ */
    background: -ms-linear-gradient(top,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* IE10+ */
    background: linear-gradient(to bottom,  rgba(0,0,0,0) 0%,rgba(0,0,0,1) 100%); /* W3C */
}

.trip-apply {
    position:absolute;
    bottom:0;
    right:0;
}